import './App.css';
import React from 'react'
import InstagramLogo from './svgs/Instagram.svg'
import FacebookLogo from './svgs/Facebook.svg'
import Plus from './svgs/Plus.svg'

function App() {
  const [fade, setFade] = React.useState(0);
  //const [grow, setGrow] = React.useState(0);

  function handleClick(event){
    setFade(1);
    setTimeout(() => {setFade(0);}, 1000);
    //setGrow(1);
  }

  return (
    <div className="App">
      <div className="MaroonDiv"></div>
      <h1 className="Explore" fade={fade}>Explore</h1>
      <div className="socialMedia">
        <img className="socialButtons" src={InstagramLogo} alt="instagram logo"/>
        <img className="socialButtons" src={FacebookLogo} alt="facebook logo"/>
      </div>
      <div className="Details">
        <button className="button"><img src={Plus} alt="Click for more Details" onClick={handleClick}/></button>
        <p >More Details</p>
        {/* <section className="DetailsExpand" grow={grow}>
          <h1>Explore</h1>
          <p className="DetailsParagraph">

          </p>
          <button><a href="#" class="close"/></button>
        </section> */}
      </div>
    </div>
  );
}

export default App;
